@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{box-sizing:border-box;background-image:linear-gradient(to left bottom, #f2b705, #f5a70f, #f6961b, #f58625, #f2762e)}button{background:#f28705;color:#fff;font-size:2.5rem;padding:1rem 2rem;border:0;outline:0;border-radius:30px;box-shadow:10px 10px 14px 1px rgba(0,0,0,0.2)}a{text-decoration:none;color:#025e73}a:hover{color:#fff}.author{font-size:3rem}.app{display:grid;grid-auto-columns:1fr 1fr;grid-template-areas:"title title" "btn-add  btn-go" "inputs .";grid-gap:1em;gap:1em;align-items:center;justify-items:center;text-align:center;padding:0 20px}.app-title{grid-area:title;display:flex;flex-direction:column;color:#025e73;width:100%;justify-content:center;align-items:center;font-size:1.5rem}.app-title h1{font-size:3rem}.app-title a{font-size:2rem}.btn-go{grid-area:btn-go;width:80%;margin:20px;background:#f2762e}.btn-go:hover{background:#f5955e}.btn-add{grid-area:btn-add;width:80%}.btn-add:hover{background:#fb9f2f}.inputs{grid-area:inputs;display:flex;flex-direction:column;flex-wrap:wrap;height:55vh}.option-input{text-align:center;font-size:2rem;margin:0.2rem;background:#037f8c;color:#fff;border:0;border-radius:30px;outline:0;box-shadow:10px 10px 14px 1px rgba(0,0,0,0.2);padding:1rem 0}.option-input::-webkit-input-placeholder{color:#025e73}.option-input:-ms-input-placeholder{color:#025e73}.option-input::-ms-input-placeholder{color:#025e73}.option-input::placeholder{color:#025e73}.result{color:#025e73;background:#fff;border-radius:30px;padding:10px;width:90%;box-shadow:10px 10px 14px 1px rgba(0,0,0,0.2)}@media screen and (max-width: 426px){html{background:#f2b705;font-size:200%;overflow-y:auto}.app{grid-auto-columns:1fr;grid-template-areas:"title" "btn-add" "btn-go" "inputs "}.inputs{flex-wrap:nowrap}.option-input{font-size:3rem}}

